const _dummy82x82H264PFrameFrameNumberIndex = 10418;
const _dummyAudioSilentPerCodec = {opus: new Uint8Array([0x68, 0x07, 0xd9, 0xab, 0xbc, 0x79, 0x87, 0xab, 0x8e, 0x4a, 0x14, 0xf3, 0xa7, 0x1c, 0xf2, 0x9a, 0x7a, 0x45, 0xa7, 0x2d, 0x44, 0x25, 0x74, 0x4c, 0xad, 0x16, 0xb3, 0x0e, 0x51, 0x57, 0xea, 0xe7, 0xad, 0x2a, 0x38, 0xce, 0x33])};
const _singleZeroByte = new Uint8Array([0x00]);
const _isAndroidChrome = BrowserDetector.browserName === 'Chrome' && BrowserDetector.isAndroid;
const _isMobileChrome = BrowserDetector.browserName === 'Chrome' && BrowserDetector.isMobile();
const _isSafari = BrowserDetector.browserName === 'Safari';
const _useCountingFrameNumber = _isMobileChrome || _isSafari;
const _pFrameFrameNumberReset = 1;
let _pFrameFrameNumber = _pFrameFrameNumberReset;

function getSurrogateVideoIFramePerCodec(codec: string): ArrayBuffer | undefined {
  switch (codec) {
    case 'H264': {
      if (_useCountingFrameNumber) {
        _pFrameFrameNumber = _pFrameFrameNumberReset;

        return _dummy82x82H264['IFrame']['buffer'];
      }

      return _dummy2x2VideoIFramePerCodec[codec]['buffer'];
    }

    case 'VP8': {
      return _dummy2x2VideoIFramePerCodec[codec]['buffer'];
    }

    case 'VP9': {
      return _dummy2x2VideoIFramePerCodec[codec]['buffer'];
    }

    default: {
      return undefined;
    }
  }
}

function getSurrogateVideoPFramePerCodec(codec: string): ArrayBuffer | undefined {
  switch (codec) {
    case 'H264': {
      if (_useCountingFrameNumber) {
        const buffer = updatePFrameFrameNumber();

        return buffer;
      }

      return _dummy2x2VideoPFramePerCodec[codec]['buffer'];
    }

    case 'VP8': {
      return _dummy2x2VideoPFramePerCodec[codec]['buffer'];
    }

    case 'VP9': {
      return _dummy2x2VideoPFramePerCodec[codec]['buffer'];
    }

    default: {
      return undefined;
    }
  }
}

function getSurrogateAudioSilentPerCodec(codec: string): ArrayBuffer | undefined {
  switch (codec) {
    case 'opus': {
      return _dummyAudioSilentPerCodec[codec]['buffer'];
    }

    default: {
      return undefined;
    }
  }
}

function getInvalidAudioData(): ArrayBuffer | undefined {
  return _singleZeroByte['buffer'];
}

function getInvalidVideoIFrame(codec: string): SurrogateFrameType {
  if ((codec === 'H264') && _useCountingFrameNumber) {
    _pFrameFrameNumber = _pFrameFrameNumberReset;

    if (_isAndroidChrome) {
      return SkipFrame;
    }
  }

  return _singleZeroByte['buffer'];
}

function getInvalidVideoPFrame(codec: string): SurrogateFrameType {
  if ((codec === 'H264') && _useCountingFrameNumber) {
    const surrogate = updatePFrameFrameNumber();

    if (_pFrameFrameNumber > _pFrameFrameNumberReset) {
      if (_isAndroidChrome) {
        return SkipFrame;
      }

      return _singleZeroByte['buffer'];
    }

    _pFrameFrameNumber++;

    return surrogate;
  }

  return _singleZeroByte['buffer'];
}

function updatePFrameFrameNumber(): ArrayBuffer {
  const byte1 = 0x9a | ((_pFrameFrameNumber & 0x0e) >> 1);
  const byte2 = 0x20 | ((_pFrameFrameNumber & 0x01) << 7);
  const buffer = _dummy82x82H264['PFrame']['buffer'];

  buffer[_dummy82x82H264PFrameFrameNumberIndex] = byte1;
  buffer[_dummy82x82H264PFrameFrameNumberIndex + 1] = byte2;
  _pFrameFrameNumber++;

  return buffer;
}

export {
  getSurrogateVideoIFramePerCodec,
  getSurrogateVideoPFramePerCodec,
  getSurrogateAudioSilentPerCodec,
  getInvalidAudioData,
  getInvalidVideoIFrame,
  getInvalidVideoPFrame,
  SurrogateFrameType,
  SkipFrame
};